.main-content {
    text-align: center;
    color: #fff;
    padding: 2rem 0;
  }
  
  .title {
    font-family: Neue Haas Unica;
font-size: 65px;
font-weight: 400;
line-height: 65.04px;
text-align: center;

  }
  
  .contact-info {
    display: flex;
    justify-content: center;
    gap: 3rem;
  }
  
  .contact, .follow {
    text-align: left;
  }
  
  .contact h3, .follow h3 {
    margin-bottom: 1rem;
    font-size: 1.2rem;
    color: #ccc;
  }
  
  .contact p {
    margin: 0.2rem 0;
    font-size: 1rem;
  }
  
  .social-icons {
    display: flex;
    gap: 10px;
  }
  
  .icon {
    font-size: 1.5rem;
    color: #fff;
    cursor: pointer;
  }
  